        .ContactItem {
            width: 100%;
            &:not(:last-child) {
                margin-bottom: 1rem;
            }
            .contact {
                display: flex;
                align-items: center;
                background-color: #191D2B;
                padding: 3rem 0;
                @media screen and (max-width: 480px) {
                    font-size: 1rem;
                }
                .right-items {
                    margin-left: 2rem;
                }
                img {
                    padding: 1rem;
                    border: 1px solid #2e344e;
                    margin-left: 2rem;
                    width: 16%;
                    @media screen and (max-width: 385px) {
                        display: none;
                    }
                }
                .right-items {
                    h6 {
                        font-size: 1.4rem;
                        font-weight: 500;
                    }
                }
            }
        }
        
        .ContactPage {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            margin-bottom: 6rem;
            @media screen and (max-width: 1270px) {
                grid-template-columns: repeat(1, 1fr);
                .map-sect {
                    width: 100%;
                    height: 50vh;
                    margin-bottom: 1rem;
                }
            }
            .contact-sect {
                width: 100%;
            }
        }
        
        .map-sect {
            width: 97%;
            padding: 1rem;
            background-color: #191D2B;
            iframe {
                width: 100%;
                height: 100%;
                @media screen and (max-width: 480px) {
                    height: 98%;
                }
            }
            .contact-sect {
                display: flex;
                justify-content: space-between;
                flex-direction: column;
            }
            .title {
                margin-bottom: 5rem;
            }
        }